@import '../../../style/variables';

.single-comic {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 0.3fr 0.7fr;
    gap: 50px;
    align-items: start;
    @media (max-width: 991.98px){
        margin-top: 30px; 
        gap: 30px;
    }
    @media (max-width: 767.98px){
        margin-top: 15px; 
        gap: 15px;
        grid-template-columns: 1fr;
    }
    &__char-img {
        position: relative;
        padding-bottom: 140%;
        @media (max-width: 767.98px){
           padding-bottom: 110%;
        }
        img{
            position: absolute;width: 100%;height: 100%;top: 0;left: 0;
            object-fit: cover;
        }
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
    }
    &__descr {
        font-size: 18px;
        line-height: 24px;
        margin-top: 25px;
        @media (max-width: 767.98px){
           margin-top: 10px;
        }
    }
}